import { Component, OnInit, NgZone, Inject } from '@angular/core';
import { NgUploaderOptions } from 'ngx-uploader';
import { FirebaseApp } from 'angularfire2';
import * as firebase from 'firebase';

import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';

import { PortfolioService } from './portfolio.service';
import { Portfolio } from './portfolio';




@Component({
  selector: 'app-add-portfolio',
  templateUrl: './add-portfolio.component.html',
  styleUrls: ['./add-portfolio.component.css']
})
export class AddPortfolioComponent implements OnInit {

  image:string;
  portfolioPost:FormGroup;
  portfolios:Portfolio[];
    key=null;
    private progress: number = 0;
    isNew:boolean = true;


  constructor(@Inject(FirebaseApp)private firebaseApp: any,
              private formbldr:FormBuilder , private ps:PortfolioService) { 
    
   



  }

    



  ngOnInit() {
  	 
        this.ps.getPortfolioList().subscribe(portfolios => {
      this.portfolios = portfolios
    });


        this.initForm();

  }



 



  initForm(){
    this.portfolioPost = this.formbldr.group({
      title:["",Validators.required],
      git:["",Validators.required],
      desc:["",Validators.required],
      tech:["",Validators.required],
      stack:["",Validators.required],


    })

  }


  onUpload(){

    if(this.image){
      const portfolio = new Portfolio(this.portfolioPost.value.title,this.portfolioPost.value.git,this.portfolioPost.value.stack,this.portfolioPost.value.desc,this.portfolioPost.value.tech,this.image)

      if(this.isNew){
            const portfolio = new Portfolio(this.portfolioPost.value.title,this.portfolioPost.value.git,this.portfolioPost.value.stack,this.portfolioPost.value.desc,this.portfolioPost.value.tech,this.image)
            this.ps.storePortfolio(portfolio);

      }else{
        this.ps.updatePortfolio(this.key,portfolio)

      }


    }
  }






  onEdit(item){
    this.key = item;
    this.isNew= false;
   
    this.portfolioPost = this.formbldr.group({
      title: [item.title , Validators.required],
      git: [item.git , Validators.required ],
      desc: [item.desc , Validators.required ],
      tech: [item.tech , Validators.required ],
      stack:[item.stack,Validators.required ]


    });

    this.image = item.image;

    this.onUpload();
  }

  onRemove(item){
    this.ps.removePortfolio(item);
  }



  fileChange(event){
        let that = this;


    // let fileList:FileList = event.target.files;
    //   console.log(fileList)
    //   let file:File = FileList[0];
    //   console.log(file)
          let file = event.target.files[0];


       const storageRef = this.firebaseApp.storage().ref('portfolio/' + file.name);

       let uploadTask = storageRef.put(file);

        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      function(snapshot) {
        let progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload is ' + progress + '% done');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload is running');
            break;
        }
      }, function(error) {
      switch (error) { 
        case 'storage/unauthorized':
          break;

        case 'storage/canceled':
          break;

        case 'storage/unknown':
          break;
      }
    }, function() {
      // Upload completed successfully, now we can get the download URL
      let downloadURL = uploadTask.snapshot.downloadURL;
      console.log('Upload done!');
    });

    storageRef.getDownloadURL().then(url => {this.image = url;
      console.log(this.image)});
    

      

    



  }

}



