import { Component, OnInit, trigger, transition, 
		 style, animate } from '@angular/core';

import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';

import { LoginService } from './login.service';

import { Router } from '@angular/router';

import { Md5 } from 'ts-md5/dist/md5';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  animations: [
  trigger('fadeInOut', [
    transition(':enter', [   // :enter is alias to 'void => *'
      style({opacity:0}),
      animate(500, style({opacity:1})) 
    ]),
    transition(':leave', [   // :leave is alias to '* => void'
      animate(500, style({opacity:0})) 
    ])
  ])
]


})
export class LoginComponent implements OnInit {

  

  constructor(private formbldr:FormBuilder,
              private ls:LoginService,
              private router:Router
) { 


  }

  show:boolean = false;
  loginForm:FormGroup;



  ngOnInit() {
  	this.initForm();
  }

  onLogin(){

    let password = Md5.hashStr(this.loginForm.value.password);
    let username = this.loginForm.value.username;
    this.ls.checkLogin().subscribe(data => {
         this.checkLogin(data,username,password)
    })

  }

  onToggle(){
  	this.show = !this.show;
  	
  }

  initForm(){
  	this.loginForm = this.formbldr.group({
  		username:["",Validators.required],
  		password:["",Validators.required]
  	})
  }


  checkLogin(data,username,password){
    if(data.username==username&&data.password==password){
      this.ls.onLogin();
      this.router.navigate(['admin'])

    }

  }

}



