import { Component, OnInit, Input, OnChanges, NgZone } from '@angular/core';

@Component({
  selector: 'app-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.css']
})
export class ResumeComponent implements OnInit,OnChanges {


	@Input('id') id ;
	animate:string='';
   progress:{[technology:string]:number} = { 'html':90,'css':90,'js':80,'python':90,'laravel':80,'angular':85,'eng':100,'hindi':90}
   progressTo:{[technology:string]:number} = { 'html':0,'css':0,'js':0,'python':0,'laravel':0,'angular':0,'eng':0,'hindi':0}

  constructor(private _ngZone: NgZone) {
  	

   }

   processWithinAngularZone() {
   
    for (let key in this.progress){
      this.progressTo[key]=0

    this._increaseProgress(() => console.log('Inside Done!'),key);

    }
  }

  ngOnInit() {
  }


  ngOnChanges(){
  	if(this.id){
  		this.animate='visible fadeInDown'
      this.processWithinAngularZone();


  	}
  }

  _increaseProgress(doneCallback: () => void ,key) {
    this.progressTo[key] += 1;
    
    if (this.progressTo[key] < this.progress[key]) {
      window.setTimeout(() => {
        this._increaseProgress(doneCallback,key);
      }, 10);
    } else {
      doneCallback();
    }
  }  



}



