import { Injectable } from '@angular/core';

import {AngularFire, FirebaseListObservable, FirebaseObjectObservable} from "angularfire2";
import 'rxjs/add/operator/map';

@Injectable()
export class PortfolioService {
	  items: FirebaseListObservable<any>;


  constructor(private af:AngularFire) {
  		  	this.items = this.af.database.list('/portfolio') as FirebaseListObservable<any[]>;


   }



   getPortfolioList(){
   	return this.items;
   }


   storePortfolio(portfolio){
   	return this.items.push(portfolio);

   }


   updatePortfolio(key,portfolio){
   	return this.items.update(key,portfolio);
   }

   removePortfolio(portfolio){
   	return this.items.remove(portfolio);
   }

}



